import {GatsbyImage, getImage} from "gatsby-plugin-image"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import React from "react"
import styled from "styled-components"
//import { Background } from 'react-parallax';

const BlogItem = styled.article`
  width: 100%;
  padding: 0 20px;
`

const BlogItemContent = styled.div`
  background-color: var(--blogPanelBG);
  border-top: 3px solid var(--primary);
  padding: 2.5rem 1.25rem;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: 768px) {
    margin-bottom: 0;
  }

  h2 {
    margin-top: 0;
    margin-bottom: 20px;
  }

  p {
    margin-bottom: 40px;
  }
`

const BlogCard = ({ blog }) => {
  const { slug, title, images, published } = blog

  // let mainImage = images[0].fluid
  const image = getImage(images[0])
  return (
    <BlogItem>
      <AniLink
        className="btn"
        cover
        bg="var(--background)"
        to={`/blogs/${slug}`}
      >
        <GatsbyImage image={image} alt="Single Post" />
        <BlogItemContent>
          <h2>{title}</h2>
          <p>{published}</p>
          <button className="btn">Read Post</button>
        </BlogItemContent>
      </AniLink>
    </BlogItem>
  )
}

export default BlogCard
